<template>
	<div id="category">
		<header>
			<div class="headerMain">
				<i class='icon iconfont icon-back' @click='$router.go(-1)'></i>
				<div class='search'>
					<van-field v-model="searchValue" placeholder="请输入.." />
					<i class="icon iconfont fdj">&#xe65c;</i>
				</div>
				<img class='more' src="https://ae01.alicdn.com/kf/H5764f904b5774ac69b0b1f531356f289c.png" alt="">
			</div>
		</header>
		<aside>
			<ul>
				<li  v-for="(val,i) in categoryList" :key='i' :class="{'on':currentCate===i}" @click='selectCategory(val.title,i)'>
					{{val.title}}
					<span class="block"></span>
				</li>
			</ul>
		</aside>
		<main>
			<div class="list" v-for="(val,index) in categoryList" :key='index' v-show='currentCate===index' >
				<div class="banner">
					<img :src="val.banner" alt="">
				</div>
				<div class="item" >
					<ul>
						<li  v-for="(item,i) in val.list" :key='i' @click='goGoodsList(item,val.id)'>
							<img :src="item.img" alt="">
							{{item.name}}
						</li>
					</ul>
						
				</div>
			</div>
		</main>
	</div>
</template>
<script type="text/javascript">
	import { Toast } from 'vant';
	import categoryList from '@/assets/json/categoryList.json';
	// https://api.myjson.com/bins/14h0hu
	export default{
		name:'category',
		data(){
			return{
				searchValue:'',
				categoryList:'',
				currentCate:0,
				vantLoad:true
			}
		},
		created(){
			Toast.loading({
				  message: '加载中...',
				  forbidClick: true,
				  loadingType: 'spinner'
			});
			this.loadData();
		},
		methods:{
			loadData(){		
				setTimeout(()=>{
					this.categoryList=categoryList.data;
					Toast.clear();
				},260)
				// this.$axios.get('https://api.myjson.com/bins/14h0hu').then(res=>{
				// 	let data=res.data;
				// 	this.categoryList=data.data;
				// 	Toast.clear();
				// })
			},
			selectCategory(title,index){
				sessionStorage.setItem('cateName',title);
				sessionStorage.setItem('cateId',index);
				this.currentCate=index;
				document.title='分类—'+title;
			},
			goGoodsList(item,goodsListId){
				sessionStorage.setItem('goodsName',item.name)
				sessionStorage.setItem('goodsListId',goodsListId)
				this.$router.push({
					name:'goodsList',
					params:{
						goodsListId:goodsListId
					}
				})
			}
		},
		activated(){
			let cateId=sessionStorage.getItem('cateId');
			let cateName=sessionStorage.getItem('cateName');
			if(cateName){
				this.selectCategory(cateName,parseInt(cateId));
			}
			else{
				this.selectCategory('办公用品',0);
			}
		},
		deactivated(){

		}
	}
</script>
<style lang='stylus' scoped>
	header
		height:1rem;
		.headerMain
			position:fixed;
			left:0;
			top:0;
			width: 92%;
			padding:0 4%;
			display:flex;
			align-items:center;
			height:inherit;
			z-index:99;
			background-color:#f1f1f1;
			.icon-back
				font-size:.48rem;
			.search
				width: 86%;
				display:flex;
				align-items:center;
				position:relative;
				.van-cell
					width: 100%;
					height: 0.64rem;
					border-radius: 15px;
					background-color: #f7f7f7;
					padding: 4px 16px;
					margin-left: 0.2rem;
				.fdj
					position:absolute;
					right:.2rem;
			.more
				width:.4rem;
				height:auto;
				margin-left.2rem;
	aside
		position:fixed;
		top:1rem;
		left:0;
		li
			background-color:#f2f2f2;
			font-size:.3rem;
			padding:.3rem;
			position:relative;
			&.on
				background-color: #fff;
				.block
					position:absolute;
					width: 5%;
					background-color:green;
					height:43%;
					left:0;
					top: 50%;
					transform: translateY(-50%);
	main
		margin-left:24%;
		margin-right:0;
		.list

			.banner
				margin:.1rem;
				img
					max-width:100%;
					height:2.6rem;
			.item
				margin-bottom:1.56rem;
				ul
					display:flex;
					flex-wrap:wrap;
					align-items:center;
					li
						width: 33.3333%;
						font-size:.28rem;
						display:flex;
						align-items:center;
						justify-content: center;
						flex-direction:column;
						margin:.16rem 0;
						img
							width: 1.06rem;
							height:1.06rem;	
					
</style>


